<template>
  <div>
    <group title="textarea">
      <x-textarea :max="20" placeholder=""></x-textarea>
    </group>
    <group title="input">
      <x-input placeholder=""></x-input>
      <x-textarea :max="200" name="description" placeholder=""></x-textarea>
    </group>
    <group title="">
      <x-textarea :max="200" name="detail" placeholder="" :show-counter="false"></x-textarea>
    </group>
    <group title="set height=200">
      <x-textarea :max="200" placeholder="" :show-counter="false" :height="200" :rows="8" :cols="30"></x-textarea>
    </group>
    <group title="autosize">
      <x-textarea placeholder="Type something" :show-counter="false" :rows="1" ref="autosize"></x-textarea>
    </group>
  </div>
</template>

<script>
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import Autosize from 'autosize'

export default {
  components: {
    XTextarea,
    Group,
    XInput
  },
  mounted () {
    Autosize(this.$refs.autosize.$refs.textarea)
  },
  beforeDestroy () {
    Autosize.destroy(this.$refs.autosize.$refs.textarea)
  }
}
</script>



